export default {
	data() {
		return {
			details: []
		};
	},

	onLoad() {
		let _this = this;
		uni.getStorage({
			key: 'details',
			success: function(res) {
				console.log(res.data);
				_this.details = res.data;
			}
		});
	},
	methods: {}
};
